import React from "react";

const LoginPage = () => {
  return (
    <div>
      <label
        style={{
          display: "block"
        }}
        htmlFor=""
      >
        Podaj Login:
        <input type="text" />
      </label>
      <label style={{ display: "block" }} htmlFor="">
        Podaj Hasło:
        <input type="password" />
      </label>
      <button
        style={{
          display: "block",
          marginTop: 20,
          border: "2px solid black",
          backgroundColor: "white",
          padding: 20
        }}
      >
        Zaloguj
      </button>
    </div>
  );
};

export default LoginPage;
